<template>
    <div class="adddetailWrap">
        <div class="rating_page">
            <header>
                <router-link to="/add">
                    <i class="el-icon-arrow-left"></i>
                </router-link>
                <span>搜索地址</span>
            </header>
            <div>
                <div class="add_detail">
                    <input type="text" placeholder="请输入小区/写字楼/学校等" class="add_detail_input" v-model="inputV">
                    <el-button class="add_detail_btn" @click="Submission">确认</el-button>
                </div>
                <div class="warnpart">为了满足商家的送餐要求，建议您从列表中选择地址</div>
                <div class="point" v-if="isShow1">
                    <p class="point_p">找不到地址？</p>
                    <p class="point_p">请尝试输入小区、写字楼或学校名</p>
                    <p class="point_p">详细地址（如门牌号）可稍后输入哦。</p>
                </div>
            </div>
            <div class="poisearch_container">
                <ul v-if="isShow2">
                    <li class="poisearch_container_li" v-for="c in search" @click="positions(c)">
                        <p class="li_p1">{{c ? c.name : ""}}</p>
                        <p class="li_p2">{{c ? c.address : ""}}</p>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Adddetail",
        data(){
            return {
                inputV:"",
                city_id:"",
                search:"",
                isShow1:true,
                isShow2:false,
            }
        },
        created(){
            this.city_id = JSON.parse(localStorage.getItem('city_id'));
            // console.log(this.city_id);
        },
        methods:{
            Submission(){
                if (this.inputV !== "") {
                    this.axios.get("/v1/pois",{
                        params:{
                            city_id:this.city_id,
                            keyword:this.inputV,
                            type:"search"
                        }
                    }).then(res=>{
                        if(res == "") {
                            this.search = [{name:"很抱歉！无搜索结果"}];
                        }else {
                            this.search = res;
                            // console.log(this.search);
                        }
                    }).catch(err=>{});
                    this.isShow1 = false;
                    this.isShow2 = true;
                }
            },
            positions(a){
                localStorage.setItem("add",JSON.stringify(a.name));
                this.inputV = a.name;
                this.$router.push("/add");
            }
        }
    }
</script>

<style scoped>
    .adddetailWrap{
        width: 100%;
        overflow: hidden;
    }
    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    header span{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }

    .rating_page{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #f2f2f2;
        z-index: 202;
        padding-top: 1.95rem;
    }

    .add_detail{
        display: flex;
        justify-content: space-between;
        background: #fff;
        padding: .5rem;
    }
    .add_detail_input{
        display: block;
        width: 11.8rem;
        padding: .4rem;
        background: #f2f2f2;
        border: 1px solid #ddd;
        border-radius: 5px;
        font-size: .6rem;
        outline: none;
    }
    .add_detail_btn{
        display: block;
        width: 3rem;
        background: #3199e8;
        font-size: .7rem;
        color: #fff;
        border-radius: 5px;
        margin-left: .2rem;
    }
    .warnpart{
        background: #fff6e4;
        font-size: .62rem;
        color: #ff883f;
        text-align: center;
        padding: .2rem 0;
    }
    .point{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 100%;
    }
    .point_p{
        width: 100%;
        text-align: center;
        font-size: .7rem;
        color: #969696;
        margin-bottom: .4rem;
        font-family: Helvetica Neue,Tahoma,Arial;
    }
    .poisearch_container{
        background: #f2f2f2;
    }
    .poisearch_container_li{
        border-bottom: 1px solid #ccc;
        padding: .4rem;
    }
    .li_p1{
        margin-bottom: .4rem;
        font-size: .65rem;
        color: #969696;
        font-family: Helvetica Neue,Tahoma,Arial;
    }
    .li_p2{
        font-size: .65rem;
        color: #969696;
        font-family: Helvetica Neue,Tahoma,Arial;
    }
</style>